<template>
	<article id="content" class="content">
		<ul class="breadcrumb">
			<li class="home"><a href="javascript:void(0);">홈</a></li>
			<li><a href="javascript:void(0);">서비스 관리</a></li>
			<li><span>템플릿 관리</span></li>
		</ul>
		<div class="title_area">
			<h2 class="mar_b15">템플릿 관리</h2>
			<span class="text_h2_desc">템플릿은 승인 완료 후 사용 및 발송이 가능합니다.</span>
			<span class="text_h2_desc">승인 심사는 영업일 기준 48시간 이내이며 내부 사정상 지체될 수 있습니다.</span>
		</div>
		<div class="rcs_bundle_wrap">
			<div class="rcs_emul_form_wrap">
				<a href="javascript:void(0);" class="btn_link btn_temp_info" @click="showHelp = true">
					<span>템플릿 작성 안내</span>
				</a>
				<div class="rcs_emul_form_cont">
					<div class="rcs_item_bundle" v-if="!$route.params.msgbaseId">
						<strong class="rcs_tit_bundle">브랜드명<span class="require">*</span></strong>
						<div class="rcs_cont_bundle" v-if="newbie == false">롯데카드</div>
						<div class="rcs_cont_bundle" v-else>
							<span class="custom_select">
								<select>
									<option>브랜드명을 선택 해주세요.</option>
								</select>
							</span>
						</div>
					</div>
					<div class="rcs_item_bundle" v-else>
						<strong class="rcs_tit_bundle">브랜드명<span class="require">*</span></strong>
						<div class="rcs_cont_bundle">롯데카드</div>
					</div>
					<div class="rcs_item_bundle">
						<strong class="rcs_tit_bundle">포맷 ID<span class="require">*</span></strong>
						<div class="rcs_cont_bundle"
							v-if="$route.params.msgbaseId || (showonly.apprResult == '저장' || showonly.apprResult == '반려' || showonly.apprResult == '승인' || showonly.apprResult == '반려(수정)')">
							UBR.xiC5B8hdbo-P7FB9t99KV08SfX0WvfIW5W08
						</div>
						<div class="rcs_cont_bundle" v-else>
							<span class="custom_input length">
								<input type="text" value="" ref="msgbaseId" maxlength="25">
								<span class="length_count">
									<em class="count active">0</em>/25
								</span>
							</span>
						</div>
					</div>
					<div class="rcs_item_bundle">
						<strong class="rcs_tit_bundle">템플릿 테마<span class="require">*</span></strong>
						<div class="rcs_cont_bundle" v-if="$route.params.msgbaseId">{{ showonly.themeType == 'THEME' ? '자유형' : '선택형'
						}}</div>
						<div class="rcs_cont_bundle" v-else>
							<span class="custom_radio">
								<input type="radio" v-model="selectThemeType" name="themeAttr" id="themeAttr01" value="선택형">
								<label for="themeAttr01">선택형</label>
							</span>
							<span class="custom_radio">
								<input type="radio" v-model="selectThemeType" name="themeAttr" id="themeAttr02" value="자유형">
								<label for="themeAttr02">자유형</label>
							</span>
						</div>
					</div>
					<div class="rcs_item_bundle">
						<strong class="rcs_tit_bundle">템플릿 속성<span class="require">*</span></strong>
						<div class="rcs_cont_bundle" v-if="$route.params.msgbaseId">{{ showonly.cardType == 'description' ||
							showonly.cardType
							== 'desc' ? '서술 템플릿' : '스타일 템플릿' }}</div>
						<div class="rcs_cont_bundle" v-else>
							<span class="custom_radio">
								<input type="radio" v-model="selectCardType" name="tempAttr" id="tempAttr01" value="desc">
								<label for="tempAttr01">서술 템플릿</label>
							</span>
							<span class="custom_radio">
								<input type="radio" v-model="selectCardType" name="tempAttr" id="tempAttr02" value="cell">
								<label for="tempAttr02">스타일 템플릿</label>
							</span>
						</div>
					</div>
					<div class="rcs_item_bundle" v-if="!$route.params.msgbaseId">
						<strong class="rcs_tit_bundle">유형<span class="require">*</span></strong>
						<div class="rcs_cont_bundle" v-if="$route.params.msgbaseId">주문</div>
						<div class="rcs_cont_bundle" v-else>
							<span class="custom_select">
								<select ref="msgbaseFormId">
									<option value="">선택하세요</option>
								</select>
							</span>
						</div>
					</div>
					<div class="rcs_item_bundle">
						<strong class="rcs_tit_bundle">템플릿명 <span class="require">*</span></strong>
						<div class="rcs_cont_bundle" v-if="$route.params.msgbaseId">Des_uplus01</div>
						<div class="rcs_cont_bundle" v-else>
							<span class="custom_input length">
								<input type="text" value="Des_uplus01" ref="msgbaseNm" maxlength="30">
								<span class="length_count"><em class="count active">10</em>/30</span></span>
						</div>
					</div>
					<div class="rcs_item_bundle" v-if="selectThemeType == '자유형'">
						<strong class="rcs_tit_bundle">제목 <span class="require">*</span></strong>
						<div class="rcs_cont_bundle" v-if="$route.params.msgbaseId">MMS유입알람 / daytime</div>
						<div class="rcs_cont_bundle" v-else>
							<ul class="rcs_editor_list">
								<li>
									<div class="box_radio_area">
										<span class="custom_radio single wide">
											<input v-model="taTitleInputType" type="radio" name="radioType01" id="type01" value="wide">
											<label for="type01"></label>
										</span>
										<span class="custom_radio single double">
											<input v-model="taTitleInputType" type="radio" name="radioType02" id="type02" value="double">
											<label for="type02"></label>
										</span>
									</div>
									<div class="rcs_cont_bundle">
										<span class="custom_input length" style="width: 200px">
											<input type="text" id="title01" ref="title01" maxlength="8" v-model="title01">
											<span class="length_count"><em class="count active">0</em>/8</span>
										</span>
										<span v-if="taTitleInputType === 'double'" class="custom_select" style="width:140px">
											<select :class="title2ActiveClass" disabled>
												<option v-if="title02 === ''" value="">변수 선택</option>
												<option v-if="title02 !== ''" value="">선택 안함</option>
											</select>
										</span>
									</div>
								</li>
								<li>
									<div class="btn_wrap mar_t30 left full_width" v-if="taTitleInputType === 'double'">
										<a href="javascript:void(0);" class="btn sml bd_black">변수 가져오기</a>
									</div>
								</li>
							</ul>
						</div>
					</div>
					<template v-if="selectCardType == 'desc'">
						<!-- Description 타입 -->
						<div class="rcs_item_bundle">
							<strong class="rcs_tit_bundle v_top">내용<span class="require">*</span></strong>
							<desc-contents-maker ref="descContents" :readonly="readonly" :descriptionContent="descriptionContent"
								:maxSize="maxDescriptionSize"></desc-contents-maker>
						</div>
					</template>
					<template v-else-if="selectCardType == 'cell'">
						<!-- Cell 타입 -->
						<div class="rcs_item_bundle">
							<strong class="rcs_tit_bundle v_top">내용<span class="require">*</span></strong>
							<cell-contents-maker ref="cellContents" :readonly="readonly"
								:cellContentsList="cellContentsList"></cell-contents-maker>
						</div>
					</template>
					<div class="rcs_item_bundle" v-if="!$route.params.msgbaseId">
						<!-- 버튼 -->
						<strong class="rcs_tit_bundle">버튼 노출 <span class="require">*</span></strong>
						<div class="rcs_cont_bundle">
							<span class="custom_radio">
								<input type="radio" v-model="buttonCount" name="button" id="button01" value="0">
								<label for="button01">미사용</label>
							</span>
							<span class="custom_radio">
								<input type="radio" v-model="buttonCount" name="button" id="button02" value="1">
								<label for="button02">1개</label>
							</span>
							<span class="custom_radio">
								<input type="radio" v-model="buttonCount" name="button" id="button03" value="2">
								<label for="button03">2개</label>
							</span>
						</div>
					</div>

					<button-maker :readonly="readonly" :count="buttonCount" />

				</div>
				<div class="rcs_item_bundle" v-if="$route.params.msgbaseId">
					<strong class="rcs_tit_bundle">등록일</strong>
					<div class="rcs_cont_bundle">2019-07-22 13:00 / 홍길동</div>
				</div>
				<div class="rcs_item_bundle" v-if="$route.params.msgbaseId && showonly.apprResult == '승인'">
					<strong class="rcs_tit_bundle">승인완료일 </strong>
					<div class="rcs_cont_bundle">2019-07-22 13:00</div>
				</div>
				<div class="rcs_item_bundle"
					v-else-if="!$route.params.msgbaseId && (showonly.apprResult == '승인' || showonly.apprResult == '반려' || showonly.apprResult == '반려(수정)')">
					<strong class="rcs_tit_bundle v_top">승인상태</strong>
					<div class="rcs_cont_bundle"
						v-if="showonly.apprResult == null || showonly.apprResult == '' || showonly.apprResult == '저장'">저장</div>
					<div class="rcs_cont_bundle" v-else>승인 (2023-12-07 04:04)<br>승인완료</div>
				</div>
				<!-- <div class="rcs_item_bundle">
					<strong class="rcs_tit_bundle">사용여부 </strong>
					<div class="rcs_cont_bundle">미사용 / 광고성 메시지 대량 발송</div>
				</div> -->
				<span class="custom_checkbox fs14 mar_t10" v-if="!$route.params.msgbaseId || showonly.apprResult == '저장'">
					<input type="checkbox" id="agree01">
					<label for="agree01" class="i_block">
						정보성 메시지만 보낼 수 있으며, 광고 등 정책에 위배되는 메시지 발송 시 템플릿 사용이 중지될 수 있음을 동의합니다.
					</label>
				</span>
				<div class="btn_wrap mar_t30 center full_width" v-if="!$route.params.msgbaseId">
					<a href="javascript:void(0);" class="btn big bd_black">취소</a>
					<a href="javascript:void(0);" class="btn big point">저장</a>
					<a href="javascript:void(0);" class="btn big point">승인요청</a>
				</div>
				<div class="btn_wrap mar_t30 center full_width" v-if="$route.params.msgbaseId">
					<a href="javascript:void(0);" class="btn big bd_black">목록</a>
					<a href="javascript:void(0);" class="btn big bd_black">삭제</a>
					<a href="javascript:void(0);" class="btn big point">수정</a>
				</div>
			</div>
			<!-- 스크롤시 고정-->
			<emulator-view-box :templateType="param.cardType" :readonly="readonly" :themeType="param.themeType == ''" />

		</div>

		<!-- 템플릿 작성 안내 팝업 -->
		<help-create-tmplt v-if="showHelp" @close="showHelp = false" />

	</article>
</template>

<script>
import ButtonMaker from '../components/ButtonMaker';
import CellContentsMaker from '../components/CellContentsMaker';
import DescContentsMaker from '../components/DescContentsMaker';
import EmulatorViewBox from '../components/EmulatorViewBox';
import HelpCreateTmplt from '../components/HelpCreateTmplt';

export default {
	name: 'newTemplate',
	data() {
		return {
			selectThemeType: '선택형',
			selectCardType: 'desc',
			taTitleInputType: 'wide',
			title01: '',
			title02: '',
			title2ActiveClass: '',
			buttonCount: 0,
			ready: false,
			newbie: true,
			readonly: false,
			showHelp: false,
			showonly: {
				cardType: null,
				themeType: null,
				apprResult: null,
				// apprResult: '승인',
				// apprYmd: null,
			},
			param: {
				themeTitle: '',
				cardType: 'desc',
				// cardType: 'cell',
				themeType: '',
				policyInfo: null,
			},
			guideInfo: {},
			descriptionContent: { text: null, styleStr: null },
			maxDescriptionSize: 0,
			cellContentsList: [],
		};
	},
	components: {
		DescContentsMaker,
		CellContentsMaker,
		ButtonMaker,
		EmulatorViewBox,
		HelpCreateTmplt,
	},
	methods: {

	}
};
</script>