<template>
	<div>
		<ul class="breadcrumb">
			<li class="home"><a href="javascript:void(0);">홈</a></li>
			<li><a href="javascript:void(0);">메시지 발송</a></li>
			<li><span>통합 메시지 발송</span></li>
		</ul>
		<div class="bundle_wrap">
			<div class="emul_form_wrap">
				<div class="emul_form_head">
					<h2 class="title">통합 메시지 발송</h2>
					<span class="text_h2_desc">선택한 통합 템플릿을 통해서 발송할 수 있습니다.</span>
				</div>
				<div class="emul_form_cont">
					<div class="item_bundle">
						<strong class="tit_bundle pad_t0">브랜드 / 템플릿</strong>
						<div class="cont_bundle">브랜드명 / 템플릿명</div>
					</div>

					<div class="tab_wrap" v-if="isLoadTemplate">
						<ul class="tab_nav">
							<li @click="myFilter($event)" tab-id="0" class="active"><a href="javascript:void(0);">메시지 작성</a></li>
							<li @click="myFilter($event)" tab-id="1"><a href="javascript:void(0);">발송설정</a></li>
						</ul>
						<div class="tab_container">
							<div class="tab_content active">
								<h2 class="blind">메시지작성 탭</h2>
								<div class="item_bundle_wrap">
									<div class="item_bundle">
										<strong class="tit_bundle pad_t0">메시지 구분</strong>
										<div class="cont_bundle">정보성</div>
									</div>
									<div class="item_bundle">
										<strong class="tit_bundle pad_t0">메시지 타입</strong>
										<div class="cont_bundle">텍스트</div>
									</div>
									<div class="item_bundle">
										<strong class="tit_bundle">템플릿 명</strong>
										<div class="cont_bundle">
											<span class="custom_input length full_width">
												<input type="text" value="위디어 템플릿 이름표시" disabled>
											</span>
										</div>
									</div>
									<div class="item_bundle">
										<strong class="tit_bundle">템플릿 ID</strong>
										<div class="cont_bundle">
											<span class="custom_input length full_width">
												<input type="text" value="Tmplt202311221117DlRV9" disabled>
											</span>
										</div>
									</div>
									<div class="item_bundle">
										<strong class="tit_bundle pad_t18">발송 순서</strong>
										<div class="cont_bundle">
											<div class="tab_wrap mgn">
												<div class="tab_number">
													<span class="tab_checkbox">
														<input type="checkbox" id="typeChkAll" value="RCS" v-model="checkedNamesDetail" disabled />
														<label for="typeChkAll">
															<div class="num" v-if="checkedNamesDetail.indexOf('RCS') + 1 > 0">
																{{ checkedNamesDetail.indexOf('RCS') + 1 }}
															</div>
															<div class="txt">RCS</div>
														</label>
													</span>
													<span class="tab_checkbox">
														<input type="checkbox" id="typeChk1" value="알림톡" v-model="checkedNamesDetail" disabled />
														<label for="typeChk1">
															<div class="num" v-if="checkedNamesDetail.indexOf('알림톡') + 1 > 0">
																{{ checkedNamesDetail.indexOf('알림톡') + 1 }}
															</div>
															<div class="txt">알림톡</div>
														</label>
													</span>
													<span class="tab_checkbox">
														<input type="checkbox" id="typeChk2" value="문자" v-model="checkedNamesDetail" disabled />
														<label for="typeChk2">
															<div class="num" v-if="checkedNamesDetail.indexOf('문자') + 1 > 0">
																{{ checkedNamesDetail.indexOf('문자') + 1 }}
															</div>
															<div class="txt">문자</div>
														</label>
													</span>
												</div>
											</div>
										</div>
									</div>

								</div>
							</div>
							<div class="tab_content">
								<h2 class="blind">발송설정 탭</h2>
								<div class="item_bundle_wrap">
									<receiver-set-box sendNum />
								</div>
							</div>
						</div>
					</div>

					<!-- 승인 프로세스 -->
					<approval-form-box goList />

				</div>
			</div>

			<!-- emulator -->
			<emulator-view-box tabNavi @changeLayerPop="changeLayerPop" />

			<!-- 테스트 발송 팝업 -->
			<test-sending-layer v-if="layerId == 'testSending'" @changeLayerId="changeLayerId" />


		</div>
	</div>
</template>

<script>
import EmulatorViewBox from '../components/EmulatorViewBox';
import ReceiverSetBox from '../components/ReceiverSetBox';
import ApprovalFormBox from '../components/ApprovalFormBox';
import TestSendingLayer from '../components/TestSendingLayer';

export default {
	name: 'itgSendInfo',
	data() {
		return {
			layerId: '',
			isLoadTemplate: true,
			// 타이틀 선택형(서술형), 타이틀 선택형(스타일), 타이틀 자유형(서술형), 타이틀 자유형(스타일), 단문형(SMS), 장문형(LMS), 프리 템플릿
			detailType: '',
			rcsDvs: '승인형',
			buttonCount: 0,
			checkedNamesDetail: ['RCS', '알림톡', '문자'],
			// 상세 수정가능 예시 
			// detailStatus: '',
			// 상세 수정 불가 예시
			detailStatus: '내부승인 요청',
		};
	},
	components: {
		EmulatorViewBox,
		ReceiverSetBox,
		ApprovalFormBox,
		TestSendingLayer,
	},
	methods: {
		myFilter: function (event) {
			var myIndex = event.target.parentElement.getAttribute('tab-id');
			var myLi = document.querySelectorAll('.emul_form_cont .tab_nav > li');
			var myTabContent = document.querySelectorAll('.emul_form_cont .tab_container > div');
			for (var i = 0; i < myLi.length; i++) {
				myLi[i].classList.remove('active');
				myTabContent[i].classList.remove('active');
			}
			myLi[myIndex].classList.add('active');
			myTabContent[myIndex].classList.add('active');
		},
		changeLayerId(layerId) {
			this.layerId = layerId;
		},
		changeLayerPop(layerId) {
			this.layerId = layerId;
		},
	}
};
</script>