import ItgTempList from '../views/ItgTempList';
import ItgTempInfo from '../views/ItgTempInfo';

export default [
  // 통합 템플릿 관리
  {
    path: '/view/tmp/itgTmp',
    name: 'itgTempList',
    component: () => import('@/modules/itgTmp/views/ItgTempMain.vue'),
    children: [
      // RCS 템플릿 관리 - 목록
      {
        path: '',
        name: 'itgTempList',
        component: ItgTempList,
      },
      // RCS 템플릿 관리 - 등록
      {
        path: 'new',
        name: 'itgTempNew',
        component: ItgTempInfo,
      },
      // RCS 템플릿 관리 - 상세
      {
        path: 'info/:userId',
        name: 'itgTempInfo',
        component: ItgTempInfo,
      },
    ],
  },
];
