import  MsgTempList  from "../views/MsgTempList";
import  MsgTempInfo  from "../views/MsgTempInfo";

export default [
	// 문자 템플릿 관리
	{
    path: '/view/tmp/msg',
    name: 'msgTempList',
    component: () => import('@/modules/smsTmp/views/MsgTempMain.vue'),
		children: [
			// 문자 템플릿 관리 - 목록
			{
        path: '',
        name: 'msgTempList',
        component: MsgTempList,
      },
			// 문자 템플릿 관리 - 등록
			{
        path: 'new',
        name: 'msgTempNew',
        component: MsgTempInfo,
      },
			// 문자 템플릿 관리 - 상세
			{
        path: 'info/:userId',
        name: 'msgTempInfo',
        component: MsgTempInfo,
      },
		],
  },


];
