<template>
	<div>
		<table class="tbl_row_type">
			<caption><strong>공지사항 관리</strong>
				<p>제목, 내용, 첨부파일</p>
			</caption>
			<colgroup>
				<col style="width:160px">
				<col style="width:auto">
			</colgroup>
			<tbody>
				<tr>
					<th scope="row">제목</th>
					<td>
						<span class="custom_input full_width">
							<input type="text">
						</span>
					</td>
				</tr>
				<tr>
					<th scope="row">내용</th>
					<td>
						<div class="custom_textarea big">
							<textarea></textarea>
						</div>
					</td>
				</tr>
				<tr>
					<th scope="row">제목</th>
					<td>
						<div class="cont_flex_bundle">
							<div class="box_add_files">
								<span class="custom_input">
									<input type="text" :value="fileName" readonly>
									<input type="file" id="file_bt01" @change="onFileChange" class="ui_skip">
									<label for="file_bt01" class="btn gray mid btn_add_files">
										<span>파일업로드</span>
									</label>
								</span>
							</div>
							<a href="javascript:void(0);" class="btn_delete"><span>삭제</span></a>
						</div>
					</td>
				</tr>
			</tbody>
		</table>
		<div class="btn_wrap center mar_t30">
			<a href="javascript:void(0)" class="btn big" @click="toBack">
        <span>목록</span>
      </a>
      <a href="javascript:void(0)" class="btn big" v-if="$route.name == 'noticeInfo'">
        <span>삭제</span>
      </a>
      <a href="javascript:void(0)" class="btn big point" v-if="$route.name == 'noticeNew'">
        <span>등록</span>
      </a>
      <a href="javascript:void(0)" class="btn big point" v-if="$route.name == 'noticeInfo'">
        <span>수정</span>
      </a>
		</div>
	</div>
</template>

<script>

export default {
	name: 'noticInfo',
	data() {
		return {
			fileName: '',
		};
	},
	components: {

	},
	methods: {
		onFileChange: function (e) {
			var files = e.target.files || e.dataTransfer.files;
			if (!files.length)
				return;
			this.createImage(files[0]);
		},
		toBack() {
      this.$router.push({ name: 'noticeList' });
    },
	}
};
</script>