				:talkChked="this.tabIndex2 == checkedNamesDetail.indexOf('알림톡')" viewType="상세"
				v-if="($route.params.userId && detailStatus == '') || ($route.params.userId && detailStatus == '')" />

			<emulator-view-box :msgType="this.msgDvs" :buttonCount="this.buttonCount"
				:talkChked="this.tabDetailIndex == checkedNamesDetail.indexOf('알림톡')" viewType="상세2"
				v-if="($route.params.userId && detailStatus == '내부승인 요청') || ($route.params.userId && detailStatus == '내부승인 완료')" />
		</div>
	</div>
</template>

<script>
import ApprovalFormBox from '../components/ApprovalFormBox';
import ButtonMaker from '../components/ButtonMaker';
import EmulatorViewBox from '../components/EmulatorViewBox';
import TempLayerPop from '../components/TempLayerPop';

export default {
	name: 'itgTempInfo',
	data() {
		return {
			branSlct: '브랜드 선택',
			branSlctDetail: '위디어',
			msgDvs: '정보성',
			msgType: '텍스트',
			slctSend: '',
			slctSend2: '',
			slctSendDetail: '프리 템플릿',
			slctSendDetail2: '단문형(SMS)',
			detailDisabledChk: false,
			checkedNames: [],
			checkedNamesDetail: ['RCS', '알림톡', '문자'],
			buttonCount: 0,
			layerId: '',
			lookup: false,
			lookup2: false,
			typeVal: '',
			tabIndex: -1,
			tabIndex2: 2,
			tabDetailIndex: 0,
			talkChked: false,
			isChked: false,
			// 상세 수정가능 예시 
			// detailStatus: '',
			// 상세 수정 불가 예시
			detailStatus: '내부승인 요청',
		};
	},
	mounted() { },
	components: {
		ButtonMaker,
		EmulatorViewBox,
		TempLayerPop,
		ApprovalFormBox,
	},
	methods: {
		changeLayerId(layerId) {
			this.layerId = layerId;
		},
		changeLookup(val, layerId) {
			this.layerId = layerId;
			this.lookup = val;
		},
		changeLookup2(val, layerId) {
			this.layerId = layerId;
			this.lookup2 = val;
		},
		chkTabName() {
			this.tabIndex = this.checkedNames.length - 1;
		},
		chkTabNameDetail() {
			this.tabIndex2 = this.checkedNamesDetail.length - 1;
		},
	},
};
</script>
