import  MemberList  from "../components/MemberList";
import  MemberInfo  from "../components/MemberInfo";

import  NoticeList  from "../components/NoticeList";
import  NoticeInfo  from "../components/NoticeInfo";

export default [
  // 회원관리 - 목록
  {
    path: '/view/mgt/usr',
    name: 'memberMain',
    component: () => import('@/modules/mgt/views/MemberMain.vue'),
		children: [
			{
        path: '',
        name: 'member',
        component: MemberList,
      },
			{
        path: 'new',
        name: 'memberNew',
        component: MemberInfo,
      },
			{
        path: 'info/:userId',
        name: 'memberInfo',
        component: MemberInfo,
      },
		],
  },
	{
    //코드 및 설정관리
    path: '/view/mgt/code',
    name: 'code',
    component: () => import('@/modules/mgt/views/CodeMain.vue'),
  },
	{
    // 공지사항관리
    path: '/view/mgt/notice',
    component: () => import('@/modules/mgt/views/NoticeMain.vue'),
    children: [
      {
        path: '',
        name: 'noticeList',
        component: NoticeList,
      },
      {
        path: 'info',
        name: 'noticeInfo',
        component: NoticeInfo,
      },
      {
        path: 'new',
        name: 'noticeNew',
        component: NoticeInfo,
      },
    ]
  },
	{
    //Api Key 관리 
    path: '/view/mgt/apikey',
    name: 'apiKey',
    component: () => import('@/modules/mgt/views/ApiKeyMain.vue'),
  },
];
