import RcsTempList from '../views/RcsTempList';
import RcsTempInfo from '../views/RcsTempInfo';

export default [
  // RCS 템플릿 관리
  {
    path: '/view/tmp/rcs',
    name: 'rcsTempList',
    component: () => import('@/modules/rcsTmp/views/RcsTempMain.vue'),
    children: [
      // RCS 템플릿 관리 - 목록
      {
        path: '',
        name: 'rcsTempList',
        component: RcsTempList,
      },
      // RCS 템플릿 관리 - 등록
      {
        path: 'new',
        name: 'rcsTempNew',
        component: RcsTempInfo,
      },
      // RCS 템플릿 관리 - 상세
      {
        path: 'info/:userId',
        name: 'rcsTempInfo',
        component: RcsTempInfo,
      },
    ],
  },
];
