import TemplateManagement from "../views/TemplateManagement"
import TemplateList from "../views/TemplateList"
import NewTemplate from "../views/NewTemplate"
import NewMassTemplate from "../views/NewMassTemplate"
import FreeTemplate from "../views/FreeTemplate"

export default [
	{
    // 템플릿 관리
    path: '/view/mgt/tmplt',
    component: TemplateManagement,
    children: [
      {
				// 목록
        path: '',
        name: 'tmpltlist',
        component: TemplateList,
        meta: {
          usingSearchCondition: true,
          shareList: ["newtmplt", "newmasstmplt", "freetmplt"]
        }
      },
      {
        path: 'show/free/:msgbaseId',
        name: 'freetmplt',
        component: FreeTemplate
      },
      {
        path: 'new',
        name: 'newtmplt',
        component: NewTemplate
      },
			{
        path: ':msgbaseId',
        name: 'detailtmplt',
        component: NewTemplate
      },
      {
        path: 'new/mass',
        name: 'newmasstmplt',
        component: NewMassTemplate
      },
    ]
  }
]
