import AdmSendList from '../views/AdmSendList';
import AdmSendItg from '../views/AdmSendItg';
import AdmSendRcs from '../views/AdmSendRcs';
import AdmSendSms from '../views/AdmSendSms';

import AdmTmpList from '../views/AdmTmpList';
import AdmTmpItg from '../views/AdmTmpItg';
import AdmTmpRcs from '../views/AdmTmpRcs';
import AdmTmpSms from '../views/AdmTmpSms';

export default [
  // 발송승인 관리
  {
    path: '/view/admin/send',
    name: 'adminSendList',
    component: () => import('@/modules/admin/views/AdminSendMain.vue'),
    children: [
      // 발송승인 관리 - 목록
      {
        path: '',
        name: 'adminSendList',
        component: AdmSendList,
      },
      // 발송승인 관리 - 상세(통합메시지발송)
      {
        path: 'itg',
        name: 'admSendItg',
        component: AdmSendItg,
      },
      // 발송승인 관리 - 상세(RCS메시지발송)
      {
        path: 'rcs',
        name: 'admSendRcs',
        component: AdmSendRcs,
      },
      // 발송승인 관리 - 상세(문자메시지발송)
      {
        path: 'sms',
        name: 'admSendSms',
        component: AdmSendSms,
      },
    ],
  },
  // 템플릿 승인 관리
  {
    path: '/view/admin/tmp',
    name: 'adminTmpList',
    component: () => import('@/modules/admin/views/AdminTmpMain.vue'),
    children: [
      // 템플릿 승인 관리 - 목록
      {
        path: '',
        name: 'adminTmpList',
        component: AdmTmpList,
      },
      // 템플릿 승인 관리 - 상세(통합템플릿)
      {
        path: 'itg',
        name: 'admTmpItg',
        component: AdmTmpItg,
      },
      // 템플릿 승인 관리 - 상세(RCS템플릿)
      {
        path: 'rcs',
        name: 'admTmpRcs',
        component: AdmTmpRcs,
      },
      // 템플릿 승인 관리 - 상세(문자템플릿)
      {
        path: 'sms',
        name: 'admTmpSms',
        component: AdmTmpSms,
      },
    ],
  },
];
